.quiz-content
{
  margin-top: 80px;
  text-align: center;
  margin-bottom: 20px;
  max-height: 100px;
}
.quiz-question
{
  font-size: 23px;
  color: #3A4E62;
  font-weight: 700;
  line-height: 1.4;
  margin-bottom: 40px;
}
.quiz-options
{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  max-height: 120px;
  min-width: 50px;
}
.quiz-options__item
{
  padding: 14px 25px;
  font-size: 15px;
  line-height: 1.2;
  border: 1px solid #3e5972;
  -webkit-box-shadow: 0 2px 3px rgba(0,0,0,0.2) inset;
          box-shadow: 0 2px 3px rgba(0,0,0,0.2) inset;
  border-radius: 3px;
  -webkit-transition: ease .3s;
  -o-transition: ease .3s;
  transition: ease .3s;
  opacity: 0;
  -webkit-transform: translateY(15px);
      -ms-transform: translateY(15px);
          transform: translateY(15px);
  cursor: pointer;
}


.quiz-content.active .quiz-options__item:nth-child(1)
{
  -webkit-animation: fadeIn ease .3s .1s forwards;
          animation: fadeIn ease .3s .1s forwards;
}
.quiz-content.active .quiz-options__item:nth-child(2)
{
  -webkit-animation: fadeIn ease .3s .2s forwards;
          animation: fadeIn ease .3s .2s forwards;
}
.quiz-content.active .quiz-options__item:nth-child(3)
{
  -webkit-animation: fadeIn ease .3s .3s forwards;
          animation: fadeIn ease .3s .3s forwards;
}
.quiz-content.active .quiz-options__item:nth-child(4)
{
  -webkit-animation: fadeIn ease .3s .4s forwards;
          animation: fadeIn ease .3s .4s forwards;
}
@-webkit-keyframes fadeIn
{
  0%
  {
    opacity: 0;
    -webkit-transform: translateY(15px);
            transform: translateY(15px);
  }
  100%
  {
    opacity: 3;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
@keyframes fadeIn
{
  0%
  {
    opacity: 0;
    -webkit-transform: translateY(15px);
            transform: translateY(15px);
  }
  100%
  {
    opacity: 3;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}




.quiz-options__item:not(:last-of-type)
{
  margin-right: 25px;
}

.quiz-options__item:active
{
  background-color: #3e5972;
  color: #fff;
}

.quiz-options__item:hover
{
  background-color: #3e5972;
  color: #fff;
}




.quiz .button
{
  margin: 0 auto;
}

